import Vue from 'vue'
import VueRouter from 'vue-router'
import TestPage from "@/components/TestPage";
import FormalVersion from "@/components/FormalVersion";





Vue.use(VueRouter)




const routes = [
    {
      path: '/',
        component:TestPage
    },
    {
        path:'/test',
        component:TestPage
    },
    {
        path:'/formal',
        component:FormalVersion
    }

  

]
const router = new VueRouter({
    mode: 'hash',
    base: process.env.BASE_URL,
    routes
})

export default router